/*=======================
       O-Circle
=========================*/

._mj9bi {
    display: flex;
    width: 10.555rem; height: 10.555rem;
    justify-content: center;
    align-items: flex-start;
    border-radius: 50%; 
    -webkit-animation: _A98vq .8s ease-in-out 1 forwards, _2UFuk .1s 1.1s forwards; 
            animation: _A98vq .8s ease-in-out 1 forwards, _2UFuk .1s 1.1s forwards;
  }
  
  ._jypmZ {
    margin: 0 auto 5.5rem;
  }
  
  /*=======================
      C-Circle Sign
  =========================*/
        
  ._33g90 {
    position: relative;
    opacity: 0;
    background: #fff;
    -webkit-animation-duration: .8s;
            animation-duration: .8s;
    -webkit-animation-delay: .2s;
            animation-delay: .2s;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
  }
  
  ._33g90::before, 
  ._33g90::after {
    content: "";
    position: absolute;
    background: inherit;
  }
  
  ._33g90::after {
    left: 100%; top: 0%;
    width: 500%; height: 95%; 
    transform: translateY(4%) rotate(0deg);
    border-radius: 0;
    opacity: 0;
    -webkit-animation: _1JOgk 0s 1.13s ease-in-out forwards;
            animation: _1JOgk 0s 1.13s ease-in-out forwards;
    z-index: -1;
  }
  
  
  /*=======================
        Sign Success
  =========================*/
   
  ._Kcgag { 
    background: rgb(56, 176, 131);
  }
  
  ._Kcgag ._33g90 {
    width: 1rem; height: 6rem;
    border-radius: 50% 50% 50% 0% / 10%;
    transform: translateX(130%) translateY(35%) rotate(45deg) scale(.11);  
    -webkit-animation-name: _1wEdp;  
            animation-name: _1wEdp;
  }
  
  ._Kcgag ._33g90::before {
     bottom: -17%;
     width: 100%; height: 50%; 
     transform: translateX(-130%) rotate(90deg);
     border-radius: 50% 50% 50% 50% / 20%;
  
  }
  
  /*--shadow--*/
  ._Kcgag ._33g90::after {
     background: rgb(40, 128, 96);
  }
   
  
  /*=======================
        Sign Failure
  =========================*/
  
  ._1hK5Y {
    background: rgb(236, 78, 75);
  }
  
  ._1hK5Y ._33g90 {
    width: 1rem; height: 7rem;
    transform: translateY(25%) rotate(45deg) scale(.1);
    border-radius: 50% 50% 50% 50% / 10%;
    -webkit-animation-name: _1AYoO;
            animation-name: _1AYoO;
  }
  
  ._1hK5Y ._33g90::before {
     top: 50%;
     width: 100%; height: 100%; 
     transform: translateY(-50%) rotate(90deg);
     border-radius: inherit;
  } 
  
  /*--shadow--*/
  ._1hK5Y ._33g90::after {
     background: rgba(175, 57, 55, .8);
  }
  
  
  /*-----------------------
        @Keyframes
  --------------------------*/
   
  /*CIRCLE*/
  @-webkit-keyframes _A98vq {
    0% { transform: scale(0); }
    
    50% { transform: scale(1.5); }
            
    60% { transform: scale(1); }
    
    100% { transform: scale(1); }
  }
  @keyframes _A98vq {
    0% { transform: scale(0); }
    
    50% { transform: scale(1.5); }
            
    60% { transform: scale(1); }
    
    100% { transform: scale(1); }
  }
  
  /*SIGN*/
  @-webkit-keyframes _1AYoO {         
    50% { opacity: 1;  transform: translateY(25%) rotate(45deg) scale(1.7); }
      
    100% { opacity: 1; transform: translateY(25%) rotate(45deg) scale(1); }
  }
  @keyframes _1AYoO {         
    50% { opacity: 1;  transform: translateY(25%) rotate(45deg) scale(1.7); }
      
    100% { opacity: 1; transform: translateY(25%) rotate(45deg) scale(1); }
  }
  
  @-webkit-keyframes _1wEdp {      
    50% { opacity: 1;  transform: translateX(130%) translateY(35%) rotate(45deg) scale(1.7); }
      
    100% { opacity: 1; transform: translateX(130%) translateY(35%) rotate(45deg) scale(1); }
  }
  
  @keyframes _1wEdp {      
    50% { opacity: 1;  transform: translateX(130%) translateY(35%) rotate(45deg) scale(1.7); }
      
    100% { opacity: 1; transform: translateX(130%) translateY(35%) rotate(45deg) scale(1); }
  }
   
  
  @-webkit-keyframes _1JOgk {
    to { opacity: 1; }
  }
   
  
  @keyframes _1JOgk {
    to { opacity: 1; }
  }
  
  @-webkit-keyframes _2UFuk {
    to { overflow: hidden; }
  }
  
  @keyframes _2UFuk {
    to { overflow: hidden; }
  }
  
  
  
  /*+++++++++++++++++++
      @Media Queries
  +++++++++++++++++++++*/
  
  @media screen and (min-width: 1300px) {
    
    HTML { font-size: 1.5625em; } /* 25 / 16 = 1.5625 */
    
  }
